import React from 'react';

import Layout from '../components/layout';
import { PrismCode } from '../utils/highlight.js';


const Documantation = () => {
    return (
        <Layout>
            <div className='sw-container'>
                <div className="sw-row">
                    <div className='sw-col-9 p-3'>
                        
                        <h1 className='f-40 mb-1 sans-serif'>Introduction</h1>
                        <p className='f-24 pt-0 mt-0 mb-50px serif'>
                            Get started with Swarup, to build theme-based responsive, <br/>
                            text-friendly sites, with SwarupCDN.
                        </p>

                        <h2 className='f-30 mb-1'>Quick Start</h2>
                        <p className='mt-0 pt-0 f-20 serif'>
                            Looking to quickly add Swarup to your project? <br/>
                            Use SwarupCDN, provided for free by the folks at StackPath. <br/>
                            Using a package manager or need to download the source files? <br/>
                            Head to the downloads page.
                        </p>

                        <h3 className='f-28 mb-1'>CSS</h3>
                        <p className='mt-0 pt-0 f-20 serif'>
                            Copy-paste the stylesheet &lt;link&gt; into your &lt;head&gt; before all other stylesheets to load our CSS.
                        </p>
                        <PrismCode
                            code={'<link rel="stylesheet" href="https://cdn.jsdelivr.net/gh/Ajay1290/Swarup/static/dist/CSS/swarup.min.css"/>'}
                            language="html"
                            plugins={["line-numbers"]}
                        />

                        
                        <h3 className='f-28 mb-1'>JS</h3>
                        <p className='mt-0 pt-0 f-20 serif'>
                            Many of our components require the use of JavaScript to function. Place the following &lt;script&gt;s near the end of your pages, right before the closing &lt;/body&gt; tag, to enable them.
                        </p>
                        <PrismCode
                            code={'<script src="https://cdn.jsdelivr.net/gh/Ajay1290/Swarup/static/dist/JS/swarup.min.js"></script>'}
                            language="html"
                            plugins={["line-numbers"]}
                        />

                        <h3 className='f-28 mb-1'>Starter template</h3>
                        <p className='mt-0 pt-0 f-20 serif'>
                        Be sure to have your pages set up with the latest design and development standards. That means using an HTML5 doctype and including a viewport meta tag for proper responsive behaviors. Put it all together and your pages should look like this:
                        </p>
                        <PrismCode
                            code={
'<!doctype html> \n \
  <html lang="en">  \n \
    <head> \n \
        <!-- Required meta tags --> \n \
        <meta charset="utf-8"> \n \
        <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no"> \n \
     \n \
        <!-- Swarup CSS --> \n \
        <link rel="stylesheet" href="https://cdn.jsdelivr.net/gh/Ajay1290/Swarup/static/dist/CSS/swarup.min.css"/> \n \
     \n \
        <title>Hello, world!</title> \n \
    </head> \n \
    <body> \n \
        <h1>Hello, world!</h1> \n \
     \n \
        <!-- Swarup JavaScript --> \n \
        <script src="https://cdn.jsdelivr.net/gh/Ajay1290/Swarup/static/dist/JS/swarup.min.js"></script> \n \
    </body> \n \
  </html>'}
                                language="html"
                                plugins={["line-numbers"]}
                            />
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default Documantation;